import { GoogleGenerativeAI } from '@google/generative-ai';
import { NextResponse } from 'next/server';

const genAI = new GoogleGenerativeAI(process.env.GOOGLE_API_KEY || '');

export async function POST(req: Request) {
  try {
    const { prompt } = await req.json();

    const model = genAI.getGenerativeModel({ model: 'gemini-pro' });

    const chatPrompt = `
      Eres un asistente de IA experto en astronomía para una web interactiva sobre exploración espacial.
      Tu objetivo es responder preguntas de los usuarios sobre cualquier tema, manteniendo un tono amigable, educativo y conciso.
      Evita el uso de markdown.
      Pregunta: ${prompt}
      Respuesta:
    `;

    const result = await model.generateContent(chatPrompt);
    const response = result.response;
    const text = response.text();

    return NextResponse.json({ response: text });
  } catch (error) {
    return NextResponse.json({ error: 'Failed to generate response' }, { status: 500 });
  }
}
